body {
  font-family: sans-serif;
}

/* Make content area fill the entire browser window */
html,
.fullscreen {
  display: inline-block;
  height: 100%;
  margin: 0;
  padding: 0;
  width: 100%;
}


* {
  box-sizing: border-box;
}

/* Center the content in the browser window */
.container {  
  margin: auto;
  text-align: center;
}

.padeqal{padding-left:0px!important;padding-right:0px!important;}
.title {
  font-size: 3rem;
}

.error {
  color: red;
}

.form-control{height:40px!important;}
.loginScreen{
  background-color: #5bbd9b;
  text-align:center;
  padding: 60px 0;
  min-height: 100vh;
}

.loginScreen form {
    margin: 25% 25% 0 25%;
}

.loginScreen h1 {
    color: #f0fefe;
    margin-bottom: 0px;
    font-size: 50px;
    font-weight: bold;
    letter-spacing: 5px;
}
.loginScreen h5 {
    color: #b3f3ea;
    margin-top: 0px;
    font-size: 18px;
    letter-spacing: 2px;
}

.registerScreen{
  background-color: #ffffff;
  width: 60%;
  margin: 0 auto;

}



.registerScreen form {
    margin: 50px 100px 0 100px;
}


.registerScreen .nameText {
  margin-top: 30px;
}

.registerScreen h4 {
  margin-top: 25px;
  text-align: left;
  margin-left: 30px;
  color: #000;
  font-weight: bold;
}

.registerScreen .gender {
  width: 50%;
}

.gender .form-control {
  text-align: center;
  cursor: pointer;

}

.gender .form-control:hover {
  background-color: #5bbd9b ;
  color: #fff;
}

.gender .select {
  background-color: #5bbd9b ;
  color: #fff;
}

.dobInstruction {
  font-size: 12px;
  color: #c7c7c7;
  margin-left: 30px;
}

a {
  text-decoration: none;
  display: inline-block;
  padding: 8px 16px;
}

a:hover {
  background-color: #5bbd9b;
  color: #fff;
}

.next {
  background-color: #ddd;
  color: #fff;
  float: right;
  text-decoration: none;
}

.round {
    border-radius: 50%;
    padding: 5px 20px 10px 20px;
    text-align: center;
    line-height: 30px;
    font-size:30px;
}

.userList {
    margin: 75px 100px 0 100px;
}

.left {
  float: left;
  border: 1px solid gray;
    width: 100%;
    text-align: left;
    margin-bottom: 0px;
}

.borderClass {
  float: left;
  border: 1px solid gray;
    width: 25%;
    text-align: left;
    margin-bottom: 0px;
}

.row, .row > .column {
  padding: 8px;
}


/* Clear floats after rows */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Content */
.content {
  background-color: white;
  padding: 10px;
}

/* Style the navigation menu */
.topnav {
  overflow: hidden;
  background-color: #333;
  position: relative;
}

/* Hide the links inside the navigation menu (except for logo/home) */
.topnav #myLinks {
  display: none;
}

/* Style navigation menu links */
.topnav a {
  color: white;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  display: block;
}

/* Style the hamburger menu */
.topnav a.icon {
  background: black;
  display: block;
  position: absolute;
  right: 20px;
  top:13px;
}

/* Add a grey background color on mouse-over */
.topnav a:hover {
  background-color: #ddd;
  color: black;
}

/* Style the active link (or home/logo) */
.active {
  background-color: #4CAF50;
  color: white;
}

.logo {
  width: 25px;
    height: 25px;
}
.nameText input{height: 40px;outline:none !important;}
.nameText input:focus{outline:none;border-color:none;}
.padeqal form button{color: #2ed49a;font-weight: bold;}
.center { text-align: center; }

.homeScreen{
  background-color: #ffffff;
  margin: 0 30% 0 30%;
  font-size: 20px;
  font-weight: bold;
}

.homeScreen .userName{
  margin-left: 40px;  
}

.homeScreen .menu {
  text-align: right;
  margin-right: 40px;
}

.homeScreen .menu:after {
  content: '\2807';
  font-size: 25px;
}

.homeScreen .headerInfo {
  width: 100%;
  background-color: #e54e3a;
  color: #fff;
}

.homeScreen .round {
    border-radius: 50%;
    padding: 0px 15px 10px 15px;
    text-align: center;
    line-height: 30px;
    background-color:#bebebe;
    color: #fff;
    font-size: 30px;
}

.homeScreen .caption {
  border: 1px solid #bebebe;
  font-weight: normal;
}

.homeScreen .captionSpace {
  margin-bottom: 15px;
}

.homeScreen img {
  width: 100%;
  height: auto;
}

.homeScreen .caption2 {
  border: 1px solid #bebebe;
  font-weight: normal;
  text-align: center;
}

.homeScreen .caption3 {
  border: 1px solid #bebebe;
  font-weight: normal;
  margin-top: 15px;
  height: 100px;
}

@media only screen and (max-width:768px) {
  .loginScreen form {margin: 40% 10% 0 10%;}
  .loginScreen h1 {color: #f0fefe;margin-bottom: 0px;font-size: 50px;font-weight: bold;letter-spacing: 5px;}
  .loginScreen h5 {color: #b3f3ea;margin-top: 0px;font-size: 18px;letter-spacing: 2px;}
  .registerScreen{width:100%;}
  .registerScreen form {margin: 100px 40px 0 40px;}
  .homeScreen {font-size: 20px;font-weight: bold;}
  .homeScreen{margin: 0px 10% 0px 10%;font-size: 10px;font-weight: bold;}
  .homeScreen .userName{margin-left: 10px;width: 100%}
  .homeScreen .menu {float: right;margin-right: 10px;margin-top: 0px;}
  .homeScreen .headerInfo {background-color: #e54e3a;color: #fff;}
  .homeScreen .round {border-radius: 50%;padding: 0px 15px 10px 15px;text-align: center;line-height: 30px;background-color:#bebebe;color: #fff;font-size: 30px;}
  .w10 {width: 20% !important;}
  .w80 {width: 60% !important;}
  .textWidth div {width: 50%}
  .textWidth1 div {width: 33%}
}

@media only screen and (max-width: 600px) {
  .loginScreen form {margin: 40% 10% 0 10%;}
  .loginScreen h1 {color: #f0fefe;margin-bottom: 0px;font-size: 50px;font-weight: bold;letter-spacing: 5px;}
  .loginScreen h5 {color: #b3f3ea;margin-top: 0px;font-size: 18px;letter-spacing: 2px;}
  .registerScreen{width:100%;}
  .registerScreen form {margin: 100px 40px 0 40px;}
  .homeScreen {font-size: 20px;font-weight: bold;}
  .homeScreen{margin: 0px 10% 0px 10%;font-size: 10px;font-weight: bold;}
  .homeScreen .userName{margin-left: 10px;    margin-top: 10px;}
  .homeScreen .menu {text-align: right;margin-right: 10px;margin-top: 0px;}
  .homeScreen .headerInfo {background-color: #e54e3a;color: #fff;}
  .homeScreen .round {border-radius: 50%;padding: 0px 15px 10px 15px;text-align: center;line-height: 30px;background-color:#bebebe;color: #fff;font-size: 30px;}
  .w10 {width: 20% !important;}
  .w80 {width: 60% !important;}
  .textWidth div {width: 50%}
  .textWidth1 div {width: 33%}
}

